import React from 'react';

const Popup = (props) => (
  <div className="popup-container">
    <div className="popup">
      <p className="uppercase">Game of throne</p>
      <p className="popup-citation">« C'est à ses manières que l'on juge un homme. »</p>
      <p className="popup-location-info">Mayfair, LONDON</p>
      <p className="popup-episode-info">Saison 3, Épisode 4</p>
      <p className="uppercase pointer">En savoir plus</p>
    </div>
    <span></span>
  </div>
);

export default Popup;


